* {
    box-sizing: border-box;
    margin: 0 auto;
}

.item__text {
    text-align: center;
    width: 100%;
    height: fit-content;
    background-color: bisque;
    opacity: 0.9;
    justify-content: space-around;
    padding: 15px;
    
}

h2 {
    word-break: break-all;
}

.container {
    display: flex;
    flex-direction: column;
    padding: 20px 20px;
  }

.countries {
    display: flex;
    padding: 20px 40px;
    gap: 20px;
    flex-wrap: wrap;
}

.Card {
    height: 440px;
}
