.CommonButton {
    outline: none;
    border: none;
    background: black;
    padding: 8px 20px;
    width: fit-content;
    margin: 0 auto;
    color: white;
    border-radius: 0;
    transition: .2s background-color;
    cursor: pointer;
}

.CommonButton:hover {
    background: rgba(0, 0, 0, 0.7);
}